
body{
		margin: 0;
		padding: 0;
}
.menu{
 	display: -webkit-box;
 	display: -ms-flexbox;
 	display: flex;
 	width: 400px;
 	height: 35px;
 	font-family: "微软雅黑";
 	font-size: 14px;
 	
 	-webkit-box-orient: horizontal;
 	
 	-webkit-box-direction: normal;
 	
 	    -ms-flex-direction: row;
 	
 	        flex-direction: row;
 	-webkit-box-pack: start;
 	    -ms-flex-pack: start;
 	        justify-content: flex-start; 
 	background-color: grey;
}
.menu-item{
 	width:50px;
 	display: block; 	
 	text-align: center;
 	line-height: 35px;
 	text-decoration: none;
 	color: white;
}



